import { Button, Form, Input, Result } from 'antd'
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { resetSignup, signup, SignupPayLoad } from '../../store/actions/auth.action'
import { AppState } from '../../store/reducers'
import { AuthState } from '../../store/reducers/auth.reducer'
import Layout  from './Layout'

const SignUp = () => {
  const dispatch = useDispatch()
  // 
  const auth = useSelector<AppState, AuthState>(state => state.auth) 
  const onFinish = (value: SignupPayLoad) => {
    dispatch(signup(value))
  }
  // 
  const [form] = Form.useForm()
  // 1.  
  useEffect(() => {
    if(auth.signup.loaded && auth.signup.success) {
      form.resetFields()
    }
  }, [auth])
  
  // 2.  
  const showSuccess = () => {
    if(auth.signup.loaded && auth.signup.success) {
      return <Result
        status="success"
        title=""
        extra={[<Button type="primary"><Link to='/signin'></Link></Button>]}
      />
    }
  }
  // 3.  
  const showFail = () => {
    if(auth.signup.loaded && !auth.signup.success) {
      return <Result
        status="warning"
        title=""
        subTitle={auth.signup.message}
      />
    }
  }
  // 4.  
  useEffect(() => {
    return () => {
      dispatch(resetSignup())
    }
  }, [])

  const signupForm = () => {
    return (
      <Form form={form} onFinish={onFinish}>
        <Form.Item name="name" label="">
          <Input />
        </Form.Item>
        <Form.Item name="password" label="">
          <Input.Password />
        </Form.Item>
        <Form.Item name="email" label="">
          <Input />
        </Form.Item>
        <Form.Item>
          <Button type='primary' htmlType='submit'></Button>
        </Form.Item>
      </Form>
    )
  }

  return (
    <Layout title='' subTitle=" ">
      {showSuccess()}
      {showFail()}
      {signupForm()}
    </Layout>
  )
}

export default SignUp