import React from 'react'
import Layout  from './Layout'
import { Button, Form, Input, Result } from 'antd'
import { signin, SigninPayLoad } from '../../store/actions/auth.action'
import { useDispatch, useSelector } from 'react-redux'
import { AppState } from '../../store/reducers'
import { AuthState } from '../../store/reducers/auth.reducer'
import { isAuth } from '../../helpers/auth'
import { Jwt } from '../../store/models/auth'
import { Redirect } from 'react-router-dom'

const SignIn = () => {
  const dispatch = useDispatch()

  const onFinish = (value: SigninPayLoad) => {
    dispatch(signin(value))
  }
  
  const auth = useSelector<AppState, AuthState>(state => state.auth)

  //  
  const showError = () => {
    if(auth.signin.loaded && !auth.signin.success) {
      return <Result
        status="warning"
        title=""
        subTitle={auth.signin.message}
      />
    }
  }
  //  
  const redirectToDashboard = () => {
    const auth = isAuth()
    if(auth) {
      const {
        user: { role }
      } = auth as Jwt

      if(role === 0) {
        return <Redirect to="/user/dashboard" />
        // 
      } else {
        // 
        return <Redirect to="/admin/dashboard" />  
      }
    }
  }

  // 
  const SignInForm = () => (
    <Form onFinish={onFinish}>
      <Form.Item name="password" label="">
        <Input.Password />
      </Form.Item>
      <Form.Item name="email" label="">
        <Input />
      </Form.Item>
      <Form.Item>
        <Button type='primary' htmlType='submit'></Button>
      </Form.Item>
    </Form>
  )

  return (
    <Layout title='' subTitle="">
      {showError()}
      {redirectToDashboard()}
      {SignInForm()}
    </Layout>
  )
}

export default SignIn
