import { Button, Form, Input, message, Select, Upload } from "antd"
import { UploadOutlined } from "@ant-design/icons"
import React, { useEffect, useState } from "react"
import Layout from "../core/Layout"
import { useDispatch, useSelector } from "react-redux"
import { getCategory } from '../../store/actions/category.action'
import { AppState } from "../../store/reducers/index"
import { CategoryState } from '../../store/reducers/category.reducer'
import { RcFile } from "antd/lib/upload"
import axios from "axios"
import { API } from "../../config"
import { isAuth } from "../../helpers/auth"
import { Jwt } from "../../store/models/auth"

const AddProduct = () => {
  const dispatch = useDispatch()
  const [file, setFile] = useState<RcFile>()

  const category = useSelector<AppState, CategoryState>(
    state => state.category
  )

  useEffect(() => {
    dispatch(getCategory())
  }, [])

  const { user, token } = isAuth() as Jwt

  const onFinish = (product: any) => {
    const formData = new FormData()
    for (let attr in product) {
      formData.set(attr, product[attr])
    }
    if (typeof file !== "undefined") {
      formData.set("photo", file)
    }
    
    axios
      .post(`${API}/product/create/${user._id}`, formData, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
      .then(
        () => {
          message.success("")
        },
        () => {
          message.error("")
        }
      )
  }

  const addProductForm = () => {
    const uploadProps = {
      accept: "image/*",
      // 
      beforeUpload: function (file: RcFile) {
        setFile(file)
        return false
      }
    }

    return (
      <Form onFinish={onFinish} initialValues={{ category: "" }}>
        <Form.Item>
          <Upload {...uploadProps}>
            <Button icon={<UploadOutlined />}></Button>
          </Upload>
        </Form.Item>
        <Form.Item name="name" label="">
          <Input />
        </Form.Item>
        <Form.Item name="description" label="">
          <Input />
        </Form.Item>
        <Form.Item name="price" label="">
          <Input />
        </Form.Item>
        <Form.Item name="category" label="">
          <Select>
            <Select.Option value=""></Select.Option>
            {category.category.result.map(item => (
              <Select.Option key={item._id} value={item._id}>
                {item.name}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item name="quantity" label="">
          <Input />
        </Form.Item>
        <Form.Item name="shipping" label="">
          <Select>
            <Select.Option value="1"></Select.Option>
            <Select.Option value="0"></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    )
  }

  return (
    <Layout title="" subTitle="">
      {addProductForm()}
    </Layout>
  )
}

export default AddProduct
